import Icon28User from '@vkontakte/icons/dist/28/user';
import Icon28Settings from '@vkontakte/icons/dist/28/settings';
import Icon28Message from '@vkontakte/icons/dist/28/message';
import Icon28Users from '@vkontakte/icons/dist/28/users';
import React from 'react';
import connect from '@vkontakte/vkui-connect';
import axios from 'axios';
// eslint-disable-next-line
import { View, Div, PanelHeader, Epic, ModalRoot, ModalPage, ModalPageHeader, List, Cell, Avatar, HeaderButton, Group,
   Tabbar, ScreenSpinner,  Panel, TabbarItem
} from '@vkontakte/vkui';
// eslint-disable-next-line
import { IS_PLATFORM_ANDROID, IS_PLATFORM_IOS } from '@vkontakte/vkui/dist/lib/platform';
import '@vkontakte/vkui/dist/vkui.css'
import Cards from './js/panels/main/Base';
import Profile from './js/panels/profile/Profile';


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      debug: true,
      activeStory: { view: 'loader', panel: 'loader' },
      activeModal: null,
      history: [{
        view: 'loader',
        panel: 'loader'
      }],
      user: undefined,
      userInterests:[],
      debugServerInfo: 'default',
      userCards: [
        {
          id: 1,
          avatar: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRCCqxQ--DqnBS4npIj956y0KQZP2H1-ZdHmsEb4pH9CiVsATBUhg",
          first_name: "Котэк",
          last_name: "Котэков",
          ages: 15,
          interests: ["Лежать", "Муркать", "Облизываться", "Кушоть", "Орать"]
        },
        {
          id: 2,
          avatar: "https://xn-----6kckiwadblcgjxwwirci4z.xn--p1ai/image/cache/catalog/vse-kartinki/sobaka/sobaka-068-800x800.jpg",
          first_name: "Пёсик",
          last_name: "Пёсиков",
          ages: 3,
          interests: ["Гавкать", "Будить рано", "Писить", "Бегать", "Бегать"]
        },
        {
          id: 3,
          avatar: "https://xn-----6kckiwadblcgjxwwirci4z.xn--p1ai/image/cache/catalog/vse-kartinki/sobaka/sobaka-068-800x800.jpg",
          first_name: "Второй пёсик",
          last_name: "Пёсиков",
          ages: 3,
          interests: ["Гавкать", "Будить рано", "Писить", "Бегать", "Бегать"]
        },
        {
          id: 4,
          avatar: "https://xn-----6kckiwadblcgjxwwirci4z.xn--p1ai/image/cache/catalog/vse-kartinki/sobaka/sobaka-068-800x800.jpg",
          first_name: "Ещё один пёсик",
          last_name: "Пёсиков",
          ages: 3,
          interests: ["Гавкать", "Будить рано", "Писить", "Бегать", "Бегать"]
        }
      ]
    };
    this.onStoryChange = this.onStoryChange.bind(this);
    this.renderDebug = this.renderDebug.bind(this);
  }
  componentDidMount() {
    connect.subscribe((e) => {
      switch (e.detail.type) {
        case 'VKWebAppGetUserInfoResult':
          this.setState({ user: e.detail.data });
          var t = this;


          axios.get(`https://salert.info/interests/GetInterests/${e.detail.data.id}`, 
          ).then((x) => {
            t.renderDebug(x.data, false);
          }).catch(function (x) {
            t.renderDebug(x, true);
          });
          break;
        default:
          console.log(e.detail.type);


      }
    });
    connect.send('VKWebAppGetUserInfo', {});
  }
  renderDebug(str, isCatch) {
    var res = isCatch?'Ошибка ':'Нормальный ответ ';
    if(!isCatch){
      this.setState({userInterests:str})
    }
    this.setState({ debugServerInfo:  res+ JSON.stringify(str), activeStory: { view: 'info', panel: 'info' } });
  }
  onStoryChange(e) {
    var c = this.state.activeStory;
    c.view = e.currentTarget.dataset.view;
    this.setState({ activeStory: c })
  }

  render() {
    const { user, activeModal, userCards, userInterests } = this.state;
   

    return (
      <Epic activeStory={this.state.activeStory.view} tabbar={
        <Tabbar>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory.view === 'info'}
            data-view='info'
            text="Профиль"
          ><Icon28User /></TabbarItem>

          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory.view === 'cards'}
            data-view='cards'
            text="Карточки"
          ><Icon28Users /></TabbarItem>


          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory.view === 'base'}
            data-view='base'
            text="Сообщения"
          ><Icon28Message /></TabbarItem>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory.view === 'settings'}
            data-view='settings'
            text="Настройки"
          ><Icon28Settings /></TabbarItem>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory.view === 'debug'}
            data-view='debug'
            text="Дебаг"
          ><Icon28Settings /></TabbarItem>
        </Tabbar>
      }>
        <View id='loader' popout={<ScreenSpinner />} activePanel="loader">
          <Panel id='loader'>
            <PanelHeader>Загрузка...</PanelHeader>
          </Panel>
        </View>
        <Profile activeModal={activeModal} userInterests={userInterests} id='info'  user={user}></Profile>


        <View id='cards' activePanel='cards'>
          <Cards id='cards' userCards={userCards} debugServerInfo={this.state.debugServerInfo}></Cards>

        </View>
        <View id='debug' activePanel='debug'>
          <Panel id='debug' >
            <PanelHeader>Debug</PanelHeader>
            <Div>
              {activeModal}
              {this.state.debugServerInfo}
            </Div>

          </Panel>
        </View>



      </Epic>
    )
  }
}

export default App;