import React from 'react';
// eslint-disable-next-line
import { Panel, Group, Button, Div, FormLayoutGroup, Radio, Checkbox, Cell, FormLayout, List, HeaderButton, ModalPageHeader, ModalPage, View, ModalRoot, Avatar, PanelHeader, Input, Select } from '@vkontakte/vkui';
import Icon16Add from '@vkontakte/icons/dist/16/add';
import Icon16Cancel from '@vkontakte/icons/dist/16/cancel';
import axios from 'axios';
import { IS_PLATFORM_ANDROID, IS_PLATFORM_IOS } from '@vkontakte/vkui/dist/lib/platform';
import Icon24Done from '@vkontakte/icons/dist/24/done';
import '@vkontakte/vkui/dist/vkui.css'
const USER_INTERESTS_MODAL = 'user_interests';

const interestsStyle = {
	backgroundColor: '#29b6f6',
	borderRadius: 10,
	padding: 5,
	margin: 5,
	whiteSpace: 'nowrap'
}
const interestsDeleteStyle = {
	backgroundColor: '#f6b629',
	borderRadius: 10,
	padding: 5,
	margin: 5,
	whiteSpace: 'nowrap'
}
const interestsInListStyle = {
	backgroundColor: '#29b6f6',
	borderRadius: 10

}

class Profile extends React.Component {

	constructor(props) {

		super(props);

		this.state = {
			userInterests: props.userInterests,
			activeModal: null,
			openInterestsModal: props.openInterestsModal,
			id: props.id,
			user: props.user,
			interests: [],
			debugInfo: "Тут дебаг",
			deNorm: "Ну тесты"
		}
		this.renderUserInterests = this.renderUserInterests.bind(this);
		this.setActiveModal = this.setActiveModal.bind(this);
		this.getInterests = this.getInterests.bind(this);
		this.renderModalInterests = this.renderModalInterests.bind(this);
	}


	componentDidMount() {
	}
	/*getSuggestions(e) {
		this.setState({ debugInfo: JSON.stringify(e.target.value) });
		if (e.target.value.length < 2)
			return;
		else {
			var t = this;
			axios.get(`https://salert.info/interests/SearchFromQuery?str=${e.target.value}`,
			).then((x) => {
				t.setState({ debugInfo: JSON.stringify(x.data), interests: x.data });
			}).catch(function (x) {
				t.setState({ debugInfo: 'Ошибка ' + JSON.stringify(x) });
			});
		}
	}*/
	renderUserInterests() {
		var listItem = [];
		this.state.userInterests.forEach((e) => {
			listItem.push(<Button style={{ ...interestsDeleteStyle }} onClick={() => this.deleteInterestFromCurrentUser(e.id)} after={<Icon16Cancel />}>{e.title}</Button>);
		});
		return <Div>
			<List style={{  lineHeight: '2.5' }}> {listItem}</List>
		</Div> 
	}


	setActiveModal(e) {

		this.setState({ activeModal: e })
	}
	addInterestToCurrentUser(id) {
		var t = this;
		var model = {
			userId: this.state.user.id,
			interestId: id
		}
		axios.post(`https://salert.info/interests/AddInterest`, model
		).then((x) => {
			t.setState({ debugInfo: `done-${JSON.stringify(x.data)}`, userInterests: x.data });
		}).catch(function (x) {
			t.setState({ debugInfo: 'Ошибка ' + JSON.stringify(x) });
		});
	}
	deleteInterestFromCurrentUser(id) {
		var t = this;
		var model = {
			userId: this.state.user.id,
			interestId: id
		}
		axios.post(`https://salert.info/interests/DeleteInterest`, model
		).then((x) => {
			t.setState({ debugInfo: `done-${JSON.stringify(x.data)}`, userInterests: x.data });
		}).catch(function (x) {
			t.setState({ debugInfo: 'Ошибка ' + JSON.stringify(x) });
		});
	}
	renderModalInterests() {
		if (this.state.interests == null) {
			return "Нет результатов"
		}
		else {
			var listItem = [];
			this.state.interests.forEach((e) => {
				if (this.state.userInterests && this.state.userInterests.find( (x) =>{return x.id == e.id})!= undefined ) {
					listItem.push(<Button style={{ ...interestsDeleteStyle }} onClick={() => this.deleteInterestFromCurrentUser(e.id)} after={<Icon16Cancel />}>{e.title}</Button>);
				}
				else {
					listItem.push(<Button style={{ ...interestsStyle }} onClick={() => this.addInterestToCurrentUser(e.id)} after={<Icon16Add />}>{e.title}</Button>);
				}
			});
			return <Div>{listItem}</Div>
		}
	}


	getInterests(e) {
		if (e.target.value.length < 2) {
			this.setState({ interests: null })
			return;
		}
		else {
			var t = this;
			axios.get(`https://salert.info/interests/SearchFromQuery?str=${e.target.value}`,
			).then((x) => {
				t.setState({ debugInfo: JSON.stringify(x.data), interests: x.data });
			}).catch(function (x) {
				t.setState({ debugInfo: 'Ошибка ' + JSON.stringify(x) });
			});
		}

	}

	render() { // eslint-disable-next-line
		const { id, user, activeModal, debugInfo, userInterests, interests, deNorm } = this.state;



		const modal1 = <ModalRoot activeModal={this.state.activeModal}>
			<ModalPage
				id={USER_INTERESTS_MODAL}
				header={
					<ModalPageHeader
						left={IS_PLATFORM_ANDROID && <HeaderButton onClick={() => { this.setActiveModal(null) }}><Icon16Cancel /></HeaderButton>}
						right={<HeaderButton onClick={() => { this.setActiveModal(null) }}>{IS_PLATFORM_IOS ? 'Готово' : <Icon24Done />}</HeaderButton>}
					>
						Фильтры
            </ModalPageHeader>
				}
			>

				<FormLayout>
					<this.renderModalInterests></this.renderModalInterests>
					<FormLayoutGroup top="Поиск" onInput={(e) => { this.getInterests(e) }}>
						<Input placeholder="Начните вводить" />
					</FormLayoutGroup>
					<Cell>{debugInfo}</Cell>
				</FormLayout>
			</ModalPage>
		</ModalRoot>





		return (
			<View id={id} modal={modal1} activePanel={id}>
				<Panel id={id} >
					<PanelHeader>
						Профиль
				</PanelHeader>
					{user &&
						<Group title="Общие данные">
							<Cell
								size="m"
								before={<Avatar src={user.photo_max_orig} size={80} />}>
								{user.first_name} {user.last_name}
							</Cell>
						</Group>
					}
					{user &&
						<Group title={`Интересы (${!userInterests ? 0 : userInterests.length}\\20)`}>
							{userInterests &&


								<this.renderUserInterests></this.renderUserInterests>


							}
							<Cell>
								<Button before={<Icon16Add />} onClick={() => { this.setActiveModal(USER_INTERESTS_MODAL) }} size="l">Добавить</Button>
							</Cell>
							<Cell>
								{deNorm}
							</Cell>
							<Cell>
								{debugInfo}
							</Cell>
							<Cell>
								<Button level="secondary" before={<Icon16Add />} size="l">Shuffle</Button>
							</Cell>

						</Group>
					}

				</Panel>
			</View>
		)
	}
}

export default Profile;